.menu-btn {
  width: 24px;
  height: 24px;
  z-index: auto;
  color: #111321;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: 14px;
  padding: 2px;
  position: relative;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .menu-btn {
    display: none;
  }
}

.menu-btn__line {
  width: 18px;
  height: 2px;
  background-color: #222;
  transition: transform .5s, top .5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.menu-btn__line--1 {
  transform-origin: center;
  transform: translate(-50%, -50%)scale(1);
}

.menu-btn__line--2 {
  top: calc(50% - 6px);
}

.menu-btn__line--3 {
  top: calc(50% + 6px);
}

.menu-btn.isActive .menu-btn__line--1 {
  transform: translate(-50%, -50%)scale(0);
}

.menu-btn.isActive .menu-btn__line--2 {
  top: 50%;
  transform: translate(-50%)rotate(45deg);
}

.menu-btn.isActive .menu-btn__line--3 {
  top: 50%;
  transform: translate(-50%)rotate(-45deg);
}

